.custom-shared-tooltip{max-width:250px !important;padding:0 !important}.custom-shared-tooltip .ui-typography{font-size:var(--caption-font-size)}.custom-shared-tooltip__label{padding:8px calc(8px * 2);display:block;background-color:var(--default-background-color);border-top-right-radius:var(--border-radius);border-top-left-radius:var(--border-radius);border-bottom:1px solid var(--color-border)}.custom-shared-tooltip__values{margin:0;padding-left:calc(8px * 2);list-style:none}.custom-shared-tooltip__values li{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:32px;padding-right:calc(8px * 2);border-bottom:1px solid var(--color-border)}.custom-shared-tooltip__values li:last-child{border-bottom:none}.custom-shared-tooltip__values li span{display:inline-block;-ms-flex-negative:0;flex-shrink:0;width:var(--dot-size);height:var(--dot-size);margin-right:8px;border-radius:100%}.custom-shared-tooltip__value-label{padding-right:calc(8px * 2);width:100%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}html[data-theme=big-dutchman] .custom-shared-tooltip{-webkit-box-shadow:0 10px 20px 5px rgba(0,0,0,.1);box-shadow:0 10px 20px 5px rgba(0,0,0,.1)}

