.ui-main-nav{position:fixed;top:0;right:0;left:0;z-index:var(--z-index-main-nav);display:-webkit-box;display:-ms-flexbox;display:flex;height:var(--main-nav-height);background-color:var(--color-primary);-webkit-backface-visibility:hidden;backface-visibility:hidden;will-change:transform;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}.ui-main-nav .ui-main-nav__logo{position:absolute;right:0;bottom:0;left:0;display:none;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-ordinal-group:1001;-ms-flex-order:1000;order:1000;width:calc(var(--main-nav-height));height:calc(var(--main-nav-height))}.ui-main-nav .ui-main-nav__logo svg{height:calc(var(--main-nav-height) - (8px * 2.5))}@media(min-width: 1152px){.ui-main-nav .ui-main-nav__logo{display:-webkit-box;display:-ms-flexbox;display:flex}}.ui-main-nav__clearer{width:100%;height:var(--main-nav-height)}

