.utydyq_farmInLandscape {
  max-width: 1750px;
  max-height: 50%;
  margin: 0 auto;
  display: none;
  position: absolute;
  top: 10%;
  bottom: 0;
  left: 0;
  right: 0;
}

@media (min-width: 1280px) {
  .utydyq_farmInLandscape {
    display: block;
  }
}

.utydyq_left, .utydyq_right {
  fill: var(--color-grey5);
  width: 30%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.utydyq_left {
  left: 0;
}

.utydyq_right {
  right: 0;
}

