@keyframes nHKZHa_pulse {
  0% {
    opacity: 1;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
  }
}

.nHKZHa_skeleton, .nHKZHa_animation, .nHKZHa_darkest, .nHKZHa_darker, .nHKZHa_small {
  height: 16px;
  background-color: var(--color-grey5);
  border-radius: var(--border-radius);
}

.nHKZHa_skeleton:before, .nHKZHa_animation:before, .nHKZHa_darkest:before, .nHKZHa_darker:before, .nHKZHa_small:before {
  content: "";
}

.nHKZHa_small {
  min-height: auto;
  height: 10px;
}

.nHKZHa_darker {
  background-color: var(--color-grey4);
}

.nHKZHa_darkest {
  background-color: var(--color-grey3);
}

.nHKZHa_animation {
  animation: 1.5s ease-in-out .5s infinite nHKZHa_pulse;
}

