.ui-navigation-bar{position:fixed;right:0;left:0;z-index:var(--z-index-navigation-bar);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;height:var(--navigation-bar-height);padding:0 var(--main-spacing);background-color:var(--color-white);border-bottom:1px solid var(--color-border)}@media(min-width: 960px){.ui-navigation-bar{left:var(--side-menu-width)}}.ui-navigation-bar__title{display:inline-block;min-width:0;margin:0 auto;padding:0 var(--main-spacing);text-align:center;width:100%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;line-height:1.5}.ui-navigation-bar__back-button.ui-navigation-bar__back-button{position:absolute;left:calc(var(--main-spacing) + (var(--side-menu-toggle-width) / 2));display:-webkit-box;display:-ms-flexbox;display:flex}.ui-navigation-bar__back-button.ui-navigation-bar__back-button .ui-button{min-width:0;padding:0}.ui-navigation-bar__back-button.ui-navigation-bar__back-button .ui-button .ui-typography{width:100%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;line-height:1.5;padding-right:8px}.ui-navigation-bar__clear{height:var(--navigation-bar-height);margin-top:calc(var(--main-spacing) * -1);margin-right:calc(var(--main-spacing) * -1);margin-bottom:var(--main-spacing);margin-left:calc(var(--main-spacing) * -1)}

