.XuIVwq_toolbar {
  z-index: 1;
  height: var(--toolbar-height);
  padding: 0 var(--main-spacing);
  background-color: var(--color-primary);
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.XuIVwq_title {
  color: var(--color-white);
}

.XuIVwq_icon {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  outline: none;
  margin-right: 24px;
  padding: 0;
  position: relative;
}

.XuIVwq_icon svg {
  fill: var(--color-white);
}

.XuIVwq_withClickListener {
  cursor: pointer;
  cursor: pointer;
}

.XuIVwq_withClickListener:after {
  content: "";
  opacity: 0;
  visibility: hidden;
  background-color: var(--color-active-background);
  width: calc(var(--icon-size) * 2);
  height: calc(var(--icon-size) * 2);
  border-radius: 100%;
  position: absolute;
  top: 50%;
  bottom: 0;
  left: 50%;
  right: 0;
  transform: translate(-50%, -50%);
}

.XuIVwq_withClickListener:active:after {
  opacity: 1;
  visibility: visible;
}

.XuIVwq_clearer {
  height: var(--toolbar-height);
}

.XuIVwq_fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

