.UWbmtW_silo, .UWbmtW_ok, .UWbmtW_warning, .UWbmtW_alarm {
  width: 60px;
  position: relative;
}

.UWbmtW_icon {
  fill: var(--color-grey1);
  width: 100%;
}

.UWbmtW_icon stop {
  stop-color: var(--color-red);
}

.UWbmtW_container .ui-icon {
  position: absolute;
}

.UWbmtW_container .ui-icon svg {
  fill: var(--color-grey1);
}

.UWbmtW_deliveryIcon {
  bottom: 0;
  left: calc((var(--icon-size)  + 4px) * -1);
  position: relative;
}

.UWbmtW_sharedIcon {
  top: 50%;
  left: calc((var(--icon-size)  + 4px) * -1);
  transform: translateY(-50%);
}

.UWbmtW_sharedIcon:after {
  content: "";
  width: 14px;
  height: 1px;
  border-top: 1px dashed var(--color-grey1);
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
}

.UWbmtW_activeIcon {
  bottom: 0;
  right: 0;
}

.UWbmtW_ok stop {
  stop-color: var(--color-green);
}

.UWbmtW_warning stop {
  stop-color: var(--color-warning);
}

.UWbmtW_alarm stop {
  stop-color: var(--color-alarm);
}

