.ui-multistep-progress-bar{position:relative;height:16px;margin-bottom:calc(8px * 5)}.ui-multistep-progress-bar .ui-progress{top:-2px;bottom:-2px}@media(min-width: 960px){.ui-multistep-progress-bar{margin-bottom:calc(8px * 8)}}.ui-multistep-progress-bar__container{position:relative;height:100%;overflow:hidden;border-radius:8px;background-color:var(--color-primary)}.ui-multistep-progress-bar__bar{position:absolute;top:0;left:0;bottom:0;background-color:var(--color-primary-light)}.ui-multistep-progress-bar__indicator{position:absolute;top:0;right:0;bottom:0;left:0;z-index:2;pointer-events:none}.ui-multistep-progress-bar__indicator-line{position:absolute;width:100%;height:100%}.ui-multistep-progress-bar__indicator-line line{stroke:var(--color-chart-reference-line)}.ui-multistep-progress-bar__indicator-arrow{position:absolute;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;width:var(--icon-size);height:var(--icon-size);border-radius:100%;background:var(--color-chart-reference-line)}html[data-theme=skov] .ui-multistep-progress-bar__indicator-arrow .ui-icon svg{fill:#fff}html[data-theme=big-dutchman] .ui-multistep-progress-bar__indicator-arrow .ui-icon svg{fill:#fff}

