.nWXzja_jumbotron {
  max-width: 650px;
  min-height: 100%;
  padding: var(--main-spacing) 0;
  text-align: center;
  margin: 0 auto;
}

@media (min-width: 960px) {
  .nWXzja_jumbotron {
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    display: -ms-flexbox;
    display: flex;
  }
}

.nWXzja_icon {
  width: auto;
  height: 180px;
  margin: 0 auto 32px;
}

.nWXzja_icon svg {
  width: auto;
  height: 180px;
}

@media (min-width: 960px) {
  .nWXzja_icon {
    height: 280px;
    margin-bottom: 64px;
  }

  .nWXzja_icon svg {
    height: 280px;
  }
}

html[data-theme="skov"] .nWXzja_icon svg {
  fill: #787878;
}

