.ui-notification-portal{position:fixed;left:0;top:0;z-index:var(--z-index-notification-portal);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;width:100%;height:100%;background:var(--color-backdrop-background);will-change:opacity}.ui-notification-portal__slider{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;margin-right:calc(var(--main-spacing) * -1);margin-left:calc(var(--main-spacing) * -1);overflow-y:visible;overflow-x:scroll;padding:4px 4px 4px calc(50vw - (var(--notification-width) / 2 - var(--main-spacing)))}.ui-notification-portal__slider>*{margin-right:var(--main-spacing)}.ui-notification-portal__slider .ui-notification:last-child{margin-right:calc(var(--main-spacing) * 2)}.ui-notification-portal__slider::after{content:"";display:block;min-width:1px;height:auto}.ui-notification-portal__slider>*{-ms-flex-negative:0;flex-shrink:0}.ui-notification-portal__slider::-webkit-scrollbar{display:none}.ui-notification-portal__slider::-webkit-scrollbar-thumb{display:none}

