.MS_ZdG_badge {
  display: block;
  position: relative;
}

.MS_ZdG_iconContainer {
  width: var(--badge-size);
  height: var(--badge-size);
  border-radius: var(--border-radius);
  margin: 0 auto;
  position: relative;
}

.MS_ZdG_icon {
  height: calc(var(--badge-size) / 2);
  width: calc(var(--badge-size) / 2);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.MS_ZdG_icon svg {
  width: calc(var(--badge-size) / 2);
  height: calc(var(--badge-size) / 2);
}

