.iupSFq_drawerSplitter {
  transition: all .35s ease-in-out;
}

@media (min-width: 960px) {
  .iupSFq_drawerSplitter {
    padding-left: calc((8px * 2)  + var(--drawer-closed-width));
  }

  .iupSFq_drawerSplitter.iupSFq_open {
    padding-left: calc((8px * 2)  + var(--drawer-width));
  }
}

