.ui-application-switcher{position:absolute;right:8px;z-index:var(--z-index-main-nav);display:grid;grid-template-columns:auto auto auto;width:calc(calc(var(--badge-size-extra-small) * 2.1) * 3);height:calc(calc(var(--badge-size-extra-small) + 60px) * 2.5);padding:var(--main-spacing) calc(8px + 4px);overflow-y:auto;background-color:var(--color-primary-dark);border-radius:var(--border-radius);border:1px solid var(--color-border-primary);-webkit-box-shadow:0 4px 4px 0 rgba(0,0,0,.08),1px 1px 4px 0 rgba(0,0,0,.04);box-shadow:0 4px 4px 0 rgba(0,0,0,.08),1px 1px 4px 0 rgba(0,0,0,.04);--scrollbar-thumb-color: rgb(255 255 255 / 50%)}.ui-application-switcher ::-webkit-scrollbar{width:calc(var(--scrollbar-width) + 2px);height:calc(var(--scrollbar-width) + 2px)}.ui-application-switcher ::-webkit-scrollbar-track{width:calc(var(--scrollbar-width) + 2px);height:calc(var(--scrollbar-width) + 2px);background-color:rgba(0,0,0,.03)}.ui-application-switcher ::-webkit-scrollbar-thumb{background-clip:padding-box;background-color:var(--scrollbar-thumb-color);border:2px solid rgba(0,0,0,0);border-radius:var(--border-radius)}.ui-application-switcher__position-up{bottom:calc(100% + (8px * -1))}.ui-application-switcher__position-down{top:calc(100% + (8px * -1))}html[data-theme=big-dutchman] .ui-application-switcher{background-color:var(--color-primary);-webkit-box-shadow:0 10px 20px 5px rgba(0,0,0,.1);box-shadow:0 10px 20px 5px rgba(0,0,0,.1)}@media(min-width: 1152px){html[data-theme=big-dutchman] .ui-application-switcher{right:auto;left:8px}}.ui-application-switcher__application{height:calc(var(--badge-size-extra-small) + 60px);border-radius:var(--border-radius)}.ui-application-switcher__application .ui-typography{position:absolute;top:100%;left:0;right:0;display:none;padding:0 4px;font-size:var(--body2-font-size);display:-webkit-box;width:100%;overflow:hidden;word-break:break-word;-webkit-line-clamp:2;-webkit-box-orient:vertical !important;text-overflow:ellipsis;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto}@media(hover: hover){.ui-application-switcher__application .ui-typography{-webkit-transition:var(--transition-duration);transition:var(--transition-duration)}.ui-application-switcher__application:hover .ui-typography{color:var(--color-white)}}

