.auMxvG_border {
  pointer-events: none;
  width: calc(100% + 8px);
  height: 100%;
  display: block;
  position: absolute;
  overflow: visible;
}

.auMxvG_border rect {
  height: 100%;
  width: calc(100% - 8px);
  fill: rgba(0, 0, 0, 0);
  stroke-width: 2px;
  stroke-dasharray: 4 6;
  display: block;
}

