.TSBQha_loader, .TSBQha_light, .TSBQha_center {
  width: var(--icon-size);
  height: var(--icon-size);
}

.TSBQha_loader div, .TSBQha_light div, .TSBQha_center div {
  width: var(--icon-size);
  height: var(--icon-size);
  border: 4px solid var(--color-primary);
  will-change: opacity;
  border-radius: 100%;
  animation: .8s ease-out infinite TSBQha_loader;
}

@keyframes TSBQha_loader {
  0% {
    opacity: .5;
    transform: scale(0);
  }

  50% {
    opacity: 1;
  }

  70% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.TSBQha_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.TSBQha_light > div {
  border-color: var(--color-white) !important;
}

.TSBQha_small, .TSBQha_small div {
  width: var(--icon-size-small);
  height: var(--icon-size-small);
}

.TSBQha_extraSmall, .TSBQha_extraSmall div {
  width: var(--dot-size);
  height: var(--dot-size);
}

.TSBQha_primaryLight > div {
  border-color: var(--color-primary-light);
}

