.UYkpTa_ul {
  padding-left: var(--main-spacing);
  margin: 0;
  list-style: none;
}

.UYkpTa_ul li {
  --dot-size: 6px;
  margin-bottom: 4px;
  position: relative;
}

.UYkpTa_ul li:last-of-type {
  margin-bottom: 0;
}

.UYkpTa_ul li:before {
  content: "";
  top: 8px;
  left: calc((8px + var(--dot-size)) * -1);
  width: var(--dot-size);
  height: var(--dot-size);
  background-color: var(--color-active);
  border-radius: 100%;
  display: inline-block;
  position: absolute;
}

