.ui-panel,.ui-panel--frame,.ui-panel--primary{position:fixed;top:0;right:0;bottom:0;left:0;z-index:var(--z-index-panel);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;overflow:hidden;margin-top:8px;-webkit-box-shadow:0 0 24px 0 rgba(0,0,0,.24);box-shadow:0 0 24px 0 rgba(0,0,0,.24);border-top-right-radius:4px;border-top-left-radius:4px;will-change:transform}@media(min-width: 960px){.ui-panel,.ui-panel--frame,.ui-panel--primary{margin-top:var(--main-spacing);margin-right:8px;margin-left:8px}}.ui-panel--primary>.ui-panel-content{margin-top:0 !important;border-top:0 !important}@media(min-width: 640px){html[data-theme=skov] .ui-panel,html[data-theme=skov] .ui-panel--primary,html[data-theme=skov] .ui-panel--frame{border-top-right-radius:20px;border-top-left-radius:20px}}

