.oknXZG_centerBoth {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.oknXZG_centerVertical {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.oknXZG_centerHorizontal {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.oknXZG_fullWidth {
  width: 100%;
}

